@import "common";
.headerimg {
  margin-top: grid_sz(0,2);
  height: grid_sz(0,3);
  margin-bottom: grid_sz(0,2); }

.fullnav {

  @include ab_full;
  @include flex_center;
  text-align: center;
  position: fixed;
  @include pos(0,0);
  z-index: 1000;
  background-color: rgba($color_white,0.95);
  max-height: 0px;
  overflow: hidden;
  opacity: 0;
  transition: 0.5s;
  .close_btn {
    position: absolute;
    right: grid_sz(0,1);
    top: grid_sz(0,1);
    @include size(30px);
    // background-color: darken($color_white,15)
    border-radius: 2px;
    opacity: 0.7;
    cursor: pointer;
    @include trans;

    &:before,&:after {
      // transform-origin: center center
      @include trans;
      display: block;
      content: "";
      width: 20px;
      height: 2px;
      background-color: $color_theme;

      @include ab_center; } }

  &.open .close_btn {
    &:before {
      transform: translate(-50%,-50%) rotate(-45deg); }
    &:after {
      transform: translate(-50%,-50%) rotate(45deg); } }


  &.open {
    opacity: 1;
    max-height: 2000px;
    overflow-y: scroll; }


  h2 {
    margin-top: grid_sz(0,2);
    margin-bottom: grid_sz(0,2); }
  * {
    color: $color_grey_dark; }
  .main_list {
    @include flex_center;
    flex-direction: column;

    h4 {
      font-size: 20px;
      font-weight: 400;
      margin-bottom: grid_sz(0,0.5); }
    &>li {
      margin-bottom: grid_sz(0,1.5); } }
  .sub_list {
    h5 {}

    li {
      display: inline-block;
      cursor: pointer;
      a {
        color: rgba($color_grey_light,0.7);
        text-decoration: none;
        @include trans; }
      padding-left: grid_sz(0,1);
      padding-right: grid_sz(0,1);
      border-right: solid 1px $color_grey_light;
      @include trans;
      &:hover a {
        color: rgba($color_grey_light,1); }
      &:last-child {
        border-right: none; }
      h5 {} } } }
